body {
  font-size: 16px;
  font-family: Verdana, sans-serif; }

.container {
  width: 300px;
  margin: auto; }

#app {
  h1 {
    font-size: 16px;
    border-bottom: 1px solid #ddd;
    padding: 16px 0; }
  ul {
    padding: 0;
    list-style: none; }
  li {
    line-height: 1.5;
    > span {
      &.done {
        text-decoration: line-through;
        color: #bbb; } } }
  input[type="text"] {
    padding: 2px; }

  .command {
    font-size: 12px;
    cursor: pointer;
    color: #08c; } }
